<template>
  <div id="radarChart" style="width:100%;height:100%;"></div>
</template>
<script>
let echarts = require("echarts/lib/echarts")
require("echarts/lib/chart/radar")
require("echarts/lib/component/polar")

export default {
  name: "echarts",
	props: {
    width:{
      type:Number,
      default:320
    },
    height:{
      type:Number,
      default:250
    },
    textList:{
      type:Array,
			default: () => []
    },
		valueList:{
			type:Array,
			default: () => []
		}
	},
  mounted() {
    this.drawRadar()
    let that = this
    window.onresize = function () {
      that.radarChart.resize()
    }
  },
  methods: {
    drawRadar() {
      let _this = this
      this.radarChart = echarts.init(document.getElementById("radarChart"))
      this.options = {
        textStyle:{
          color:'#666',
          fontSize:'100%'
        },
        radar:{
          indicator:_this.textList,
          splitArea:{
            show:false
          },
          splitLine:{
            lineStyle:{
              color:'#ffcaca',
              width:5
            }
          },
          splitNumber:4,
          axisLine:{
            lineStyle:{
              color:'#ffcaca',
              width:5,
            }
          }          
        },
        series: [
          {
            type: "radar",            
            itemStyle: {
              normal: {
                color: "#ffc6c6",
                areaStyle: {
                  color: "#ffc6c6"
                }
              }
            },
            data: [{
              value: _this.valueList
            }]            
          }
        ]
      }
    }
  },
  watch:{
    valueList:function(val){
      if(val){
        this.radarChart.setOption(this.options)
      }
    }
  }
}
</script>
<style lang="scss" scoped>
#radarCharts {
  width: 100%;
  height: 400px;
}
</style>



