<template>
  <div class="page-container m-result">
    <div class="result-hd">
      <header-back :isEmit="true" @handleClick="backApp"/>
    </div>
    <div class="result-items">
      <div class="wrap-scroll">
        <div class="bd-header">
          <span class="bd-score">你本次测评分为<em class="t-red t-strong">{{score}}</em></span>
          <span class="t-red" @click="subjectTo">题目解析 &gt;</span>
        </div>
        <div class="bd-echarts">
          <h-radar :textList="radarText" :valueList="radarValue"/>
        </div>
        <div class="bd-desc" v-for="(v,k) in resultDatas.analyseResult" :key="k" v-html="replaceText(v)"></div>
        <div class="bd-desc" v-html="replaceText(resultDatas.courseResult)"></div>
      </div>
    </div>
    <div class="result-items result-fd">
      <span class="fd-icon"><img src="./img/icon.png" alt="icon" class="icon-img">为您推荐</span>
      <img src="./img/book.png" alt="行测" class="fd-img">
      <div class="fd-section">
        <h4 class="fd-title">步知公考《行测75分必练1000题（共5本）》</h4>
        <p>风暴羚羊老师出品</p>
        <a :href="buyUrl" class="fd-submit">立即购买 &gt;&gt;</a>
      </div>
    </div>
  </div>
</template>

<script>
import HeaderBack from '@/components/HeaderBack'
import HRadar from '@/components/Radar'

import { Subject } from '@/assets/api/subject'
import { getStore,replaceContent } from '@/assets/js/utils'
import { config } from '@/assets/js/config'

export default {
  name: 'result',
  components: {
    HeaderBack,
    HRadar
  },
  data () {
    return {
      uid: getStore(config.USERID),
      resultDatas:{},           // 测评数据
      score:0,                  // 分数
      buyUrl:config.buyUrl,  // 购买地址
      radarText:[],               // 雷达图节点
      radarValue:[]   // 雷达图数据
    }
  },
  created(){
    this.$nextTick(()=>{
      this._initResult()
    })
  },
  methods:{
    _initResult(){
      Subject.getResult({
        userId: this.uid
      }).then(res=>{
        if(res.code == 1){
          this.resultDatas = res.data
          this.score = this.resultDatas.fraction
          this.resultDatas.typeResult.forEach(v=>{
            this.radarText.push({
              name:v.value,
              max:5,
            })
            this.radarValue.push(v.rightNum)
          })
        }
      })
    },
    // 文本换行替换
		replaceText(text){
			return replaceContent(text)
    },
    // 点击 题目解析
    subjectTo(){
      this.$router.push({name:'subject'})
    },
    // 返回 app
    backApp(){
      try{
        window.buzhi.appGoBack("back")
      }catch(err){
        console.warn(err)
      }
      try{
        window.webkit.messageHandlers.appGoBack.postMessage("back")
      }catch(err){
        console.warn(err)
      }
    }  
  }
}
</script>

<style lang="scss" scoped>
@import "@/assets/style/variable.scss";
.m-result{  
  background:linear-gradient(#fd7b58,#ff4343);
  padding:26px;
  box-sizing: border-box;
  .result-hd{
    position: absolute;
    left:0;
    top:0;
    z-index:10;
  }
  .result-items{
    padding:30px 34px;
    background-color:#fff;
    border-radius:10px;
    line-height: 50px;
    font-size:24px;
    color:#666;
    box-sizing: border-box;
  }
  .wrap-scroll{
    height:calc(100vh - 340px - 78px - 60px);
    overflow-x:hidden; 
    overflow-y: auto;
  }
  .t-red{
    color:$cl-theme;
  }
  .t-strong{
    font-size:42px;
    font-weight:600;
  }
  .t-bold{
    font-size:32px;
    font-weight:600;
  }
  .bd-header{
    display: flex;
    align-items: center;
    position: relative;
    .bd-score{
      flex:1;
    }
  }
  .bd-echarts{
    height:400px;
  }
  .bd-desc{
    margin-top:20px;
    font-size:28px;    
  }
  .result-fd{
    position: absolute;
    bottom:26px;
    left:26px;
    right:26px;
    height:340px;
    border:3px solid #eee;
  }
  .fd-icon{
    position: absolute;
    top:15px;
    left:-30px;
    z-index: 10;
    padding-left:13px;
    box-sizing: border-box;
    font-size:28px;
    font-weight: 600;
    color:#101010;
    line-height: 60px;
    .icon-img{
      position: absolute;
      left:0;
      top:0;
      width:158px;
      height: 69px;
      z-index: -1;
    }
  }
  .fd-img{
    position: absolute;
    right:0;
    bottom:0;
    width:373px;
  }
  .fd-section{
    position: relative;
    z-index:2;
  }
  .fd-title{
    margin-top:60px;
    width:320px;
    font-size:30px;
    line-height: 1.4;
    font-weight: 600;
    color:#101010;
  }
  .fd-submit{
    margin-top:26px;
    display: inline-block;
    width:186px;
    height: 60px;
    text-align: center;
    line-height:60px;
    color:#fff;
    border-radius: 60px;
    background:linear-gradient(#fd7b58,#ff4343);
  }  
}
</style>
<style lang="scss">
@import "@/assets/style/variable.scss";
.m-result .bd-desc strong{
  color:$cl-theme;
}
</style>



